.footer {
  padding: 100px 0 40px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    margin-bottom: 30px;
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
  &__logo {
    font-size: 30px;
    line-height: 120%;
    color: #BFADA3;
  }
  &__tel {
    font-size: 30px;
    line-height: 120%;
    color: #BF755A;
    margin-top: 10px;
    display: block;
  }
  &__list {
    li {
      font-size: 16px;
      line-height: 120%;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__btn {
    width: 235px;
    height: 50px;
    border-radius: 42px;
    font-size: 20px;
    line-height: 120%;
  }
}

@media screen and (max-width: 992px) {
  .footer {
    &__logo {
      font-size: 24px;
    }
    &__tel {
      font-size: 20px;
    }
    &__btn {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    &__logo {
      font-size: 20px;
    }
    &__btn {
      height: 40px;
    }
  }
}

@media screen and (max-width: 650px) {
  .footer {
    padding: 40px 0;
    &__items {
      flex-direction: column;
    }
    &__item {
      margin: 0;
      &:not(:last-child) {
        margin: 0 0 30px 0;
      }
    }
  }
}