.consultation {
  padding: 0 0 130px 0;
  position: relative;
  &__img2 {
    position: absolute;
    z-index: -1;
    right: 0;
    bottom: 300px;
    max-width: 380px;
  }
  &__img {
    max-width: 690px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 400px;
      height: 400px;
      background: linear-gradient(180.2deg, #E41311 0.17%, #690115 61.85%);
      border: 6.45px solid #000000;
      border-radius: 50%;
      filter: blur(70px);
    }
    img {
      width: 100%;
      position: relative;
      z-index: 2;
      display: block;
      padding-top: 50px;
    }
  }
  &__title {
    font-size: 64px;
    line-height: 105%;
    text-transform: uppercase;
    background: linear-gradient(106.09deg, #6A0216 -3.61%, #BB1415 77.08%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
    margin-top: -100px;
    margin-bottom: 80px;
    position: relative;
    z-index: 2;
  }
  &__subtitle {
    display: flex;
    align-items: center;
    span {
      font-size: 36px;
      line-height: 110%;
      color: #FBFDFC;
      margin-left: 20px;
    }
    i {
      font-style: normal;
    }
    img {
      width: 80px;
      flex-shrink: 0;
    }
  }
  &__subtitle2 {
    font-size: 36px;
    line-height: 110%;
    color: #FBFDFC;
    span {
      margin-top: 20px;
    }
  }
  &__group {
    &:not(:last-child) {
      margin-bottom: 50px;
    }
  }
  &__list {
    margin-top: 40px;
    li {
      display: flex;
      align-items: center;
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
    li span {
      font-size: 32px;
      line-height: 100%;
      margin-left: 35px;
    }
    li img {
      width: 45px;
      flex-shrink: 0;
    }
  }
  &__list2 {
    margin-top: 70px;
    li {
      display: flex;
      align-items: center;
      font-size: 32px;
      line-height: 100%;
      padding-left: 74px;
      position: relative;
      &:not(:first-child) {
        margin-top: 30px;
      }
      &:after {
        content: '';
        position: absolute;
        top: 2px;
        left: 0;
        width: 30px;
        height: 30px;
        background: linear-gradient(180deg, #191654 0%, #614AD3 100%);
        border-radius: 50%;
      }
    }
  }
  &__price {
    font-size: 36px;
    line-height: 105%;
    text-transform: uppercase;
    color: #A11815;
    margin-top: 100px;
    text-align: center;
    span {
      margin-left: 50px;
    }
  }
  &__info {
    font-size: 32px;
    line-height: 105%;
    color: #ffffff;
    margin-top: 50px;
    text-align: center;
    span {
      display: block;
      margin-top: 18px;
    }
  }
}

@media screen and (max-width: 992px) {
  .consultation {
    &__img2 {
      max-width: 130px;
    }
    &__img {
      max-width: 600px;
    }
    &__title {
      font-size: 48px;
    }
    &__subtitle {
      img {
        width: 50px;
      }
      span {
        font-size: 32px;
      }
    }
    &__subtitle2 {
      font-size: 32px;
    }
    &__list li {
      span {
        font-size: 24px;
        margin-left: 20px;
      }
      img {
        width: 30px;
      }
    }
    &__list2 {
      li {
        padding-left: 50px;
      }
    }
    &__price {
      font-size: 32px;
     span {
       margin-left: 32px;
     }
    }
    &__info {
      font-size: 28px;
    }
  }
}

@media screen and (max-width: 768px) {
  .consultation {
    padding: 0 0 100px 0;
    &__img {
      max-width: 500px;
      &:after {
        width: 300px;
        height: 300px;
      }
    }
    &__group {
      &:not(:first-child) {
        margin-bottom: 35px;
      }
    }
    &__title {
      font-size: 36px;
      margin-bottom: 35px;
    }
    &__subtitle {
      img {
        width: 35px;
      }
      span {
        font-size: 24px;
      }
    }
    &__subtitle2 {
      font-size: 24px;
    }
    &__list li {
      img {
        width: 20px;
      }
      span {
        font-size: 18px;
      }
    }
    &__list2 {
      margin-top: 40px;
      li {
        font-size: 18px;
        padding-left: 40px;
        &:after {
          top: 0;
          width: 20px;
          height: 20px;
        }
      }
    }
    &__price {
      font-size: 28px;
    }
    &__info {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 576px) {
  .consultation {
    &__title {
      font-size: 32px;
    }
    &__subtitle {
      span {
        font-size: 20px;
      }
    }
    &__subtitle {
      font-size: 20px;
    }
    &__price {
      font-size: 20px;
      margin-top: 50px;
    }
    &__info {
      font-size: 18px;
      margin-top: 35px;
      span {
        margin-top: 10px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .consultation {
    &__img2 {
      bottom: 200px;
    }
    &__img {
      img {
        padding-top: 30px;
      }
    }
    &__img {
      max-width: 305px;
      &:after {
        width: 200px;
        height: 200px;
        filter: blur(40px);
      }
    }
    &__title {
      font-size: 28px;
      margin-top: -50px;
    }
    &__subtitle {
      span {
        font-size: 13px;
      }
    }
    &__subtitle2 {
      font-size: 13px;
    }
    &__list {
      margin-top: 30px;
      li {
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
      li span {
        font-size: 13px;
      }
    }
    &__list2 {
      margin-top: 30px;
      li {
        font-size: 13px;
        &:not(:first-child) {
          margin-top: 15px;
        }
        &:after {
          width: 10px;
          height: 10px;
        }
      }
    }
    &__price {
      display: flex;
      justify-content: space-between;
    }
    &__info {
      font-size: 14px;
    }
  }
}