.manual {
  padding: 50px 0;
  position: relative;
  &__img {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    max-width: 743px;
    img {
      width: 100%;
    }
    &-mob {
      display: none;
    }
  }
  &__title {
    font-size: 51px;
    line-height: 105%;
    color: #F25E5E;
    text-transform: uppercase;
    text-align: center;
    span {
      position: relative;
      z-index: 1;
    }
  }
  &__desc {
    max-width: 1170px;
    margin: 30px auto 0;
    border: 3px solid #BFADA3;
    padding: 30px;
  }
  &__text {
    font-size: 28px;
    line-height: 105%;
    color: #FFFFFF;
    span {
      text-transform: uppercase;
    }
    &2 {
      color: #BFADA3;
    }
    &:not(:first-child) {
      margin-top: 25px;
    }
  }
  &__sun {
    color: #E7A836;
  }
  &__icon {
    text-align: center;
    margin-top: 25px;
  }
  &__price {
    font-size: 32px;
    line-height: 105%;
    color: #F25E5E;
    text-align: center;
    margin-top: 30px;
    span {
      margin-left: 32px;
    }
  }
  &__info {
    font-size: 28px;
    line-height: 150%;
    text-align: center;
    margin-top: 25px;
  }
}

@media screen and (max-width: 992px) {
  .manual {
    &__img {
      max-width: 650px;
    }
    &__title {
      font-size: 42px;
    }
    &__text {
      font-size: 24px;
    }
    &__price {
      font-size: 28px;
    }
    &__info {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 768px) {
  .manual {
    &__img {
      max-width: 500px;
    }
    &__title {
      font-size: 36px;
    }
    &__desc {
      padding: 20px;
    }
    &__text {
      font-size: 20px;
    }
    &__price {
      font-size: 24px;
    }
    &__info {
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .manual {
    padding: 0 0 50px 0;
    &__img {
     display: none;
    }
    &__img-mob {
      display: block;
      width: 100%;
      height: 306px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &__title {
      position: relative;
      font-size: 28px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -15px;
        width: calc(100% + 30px);
        height: 100%;
        background: #000000;
        filter: blur(20px);
      }
    }
    &__text {
      font-size: 18px;
    }
    &__price {
      font-size: 20px;
    }
    &__info {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 450px) {
  .manual {
    &__title {
      font-size: 24px;
    }
    &__desc {
      border: none;
      padding: 0;
    }
    &__text {
      font-size: 15px;
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
    &__icon {
      margin-top: 15px;
      img {
        width: 10px;
        height: auto;
      }
    }
    &__price {
      display: flex;
      justify-content: space-between;
      font-size: 16px;
    }
    &__info {
      font-size: 15px;
    }
  }
}