.btn-orange {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #BF755A;
  &:hover {
    background: #ff875c;
    color: #ffffff;
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}
.form {
  &-group {
    margin: 0;
    padding: 0;
    border: none;
    input {
      border: none;
      border-bottom: 1px solid #BFADA3;
      background: transparent;
      font-size: 25px;
      line-height: 120%;
      color: #BFADA3;
      align-items: start;
      &::placeholder {
        color: rgba(191, 173, 163, 0.5);;
      }
    }
  }
  &-btn {
    font-size: 20px;
    line-height: 120%;
    background: #A61420;
    border-radius: 100px;
    color: #BFADA3;
    &:hover {
      background: #e80013;
    }
  }
  &-textarea {
    border: none;
    border-bottom: 1px solid #BFADA3;
    background: transparent;
    font-size: 25px;
    line-height: 120%;
    color: #BFADA3;
    resize: none;
    padding: 0;
    margin: 0;
    &::placeholder {
      color: rgba(191, 173, 163, 0.5);;
    }
  }
}
.nav {
  padding: 50px;
  &__list li {
    font-size: 20px;
    line-height: 120%;
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
  &__btn {
    width: 200px;
    height: 50px;
  }
}
.modal {
  background: rgba(#000, .4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 568px;
    max-width: 100%;
    position: relative;
    background: linear-gradient(134.86deg, #232323 11.15%, #131313 48.41%);
    box-shadow: inset 13.7255px 13.7255px 20.5882px rgba(0, 0, 0, 0.6);
    border-radius: 13.7255px;
    padding: 58px 100px;
    margin: 0 auto;
    @media screen and (max-width: 992px) {
      margin: 50px auto 0;
      max-width: calc(100% - 30px);
    }
  }
  &__content {
    text-align: center;
  }
  &__title {
    font-size: 40px;
    line-height: 120%;
    text-transform: uppercase;
    padding-bottom: 40px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 5px;
      background: linear-gradient(180deg, #3E080C -383.33%, #A61420 383.33%);
      opacity: 0.9;
      box-shadow: inset 2.99223px 2.99223px 2.99223px rgba(0, 0, 0, 0.25);
      border-radius: 11.9689px;
    }
  }
  &__desc {
    font-size: 22px;
    line-height: 105%;
    text-transform: uppercase;
    margin-top: 40px;
  }
  &__link {
    display: flex;
    justify-content: center;
    margin-top: 40px;
    position: relative;
    z-index: 2;
    &-back {
      display: flex;
      align-items: center;
      svg {
        transition: all 0.5s;
      }
      span {
        font-size: 28px;
        line-height: 120%;
        margin-left: 15px;
        position: relative;
        padding-bottom: 2px;
        border-bottom: 0.5px solid #BFADA3;
      }
      &:hover {
        span {
          border-bottom: 0.5px solid #A61420;
        }
        svg path {
          fill: #A61420;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .modal {
    &__dialog {
      padding: 90px 40px 30px;
    }
    &__title {
      font-size: 32px;
    }
    &__desc {
      font-size: 20px;
    }
    &__link-back {
      span {
        font-size: 24px;
      }
      svg {
        width: 30px;
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .form {
    &-group {
      input {
        font-size: 20px;
      }
    }
    &-btn {
      font-size: 16px;
    }
    &-textarea {
      font-size: 20px;
    }
  }
  .modal {
    &__title {
      font-size: 28px;
    }
    &__desc {
      font-size: 18px;
    }
    &__link-back {
      span {
        font-size: 20px;
      }
      svg {
        width: 25px;
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .form {
    &-group {
      input {
        height: 20px;
        font-size: 15px;
        border-bottom: 1px solid #720E16;
      }
    }
    &-textarea {
      height: 20px;
      font-size: 15px;
      border-bottom: 1px solid #720E16;
    }
    &-btn {
      font-size: 13px;
    }
  }
  .modal {
    &__title {
      font-size: 25px;
    }
    &__desc {
      font-size: 14px;
    }
    &__link-back {
      span {
        font-size: 12px;
        margin-left: 7px;
      }
      svg {
        width: 13px;
        height: auto;
      }
    }
  }
}