.main {
  padding: 60px 0;
  &__top {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  &__link {
    svg {
      transition: all 0.5s;
    }
    &:hover {
      svg path {
        stroke: #A61420;
      }
    }
    span {
      font-size: 36px;
      line-height: 120%;
      margin-top: 15px;
      display: block;
      text-transform: uppercase;
    }
  }
  &__img {
    max-width: 700px;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    max-width: 810px;
    margin: -250px auto 0;
  }
  &__btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
    margin-top: 75px;
  }
  &__btn {
    &:hover {
      svg path {
        stroke: #A61420;
        fill: #A61420;
      }
    }
  }
}
.about {
  padding: 25px 0 185px;
  &__title {
    font-size: 200px;
    line-height: 120%;
    color: rgba(191, 173, 163, 0.05);
    background: #010101;
    text-align: center;
    margin-top: -125px;
    pointer-events: none;
  }
  &__subtitle {
    font-size: 64px;
    line-height: 120%;
    text-align: right;
    color: #F25E5E;
    margin-top: -70px;
  }
  &__img {
    max-width: 600px;
    height: 100%;
    position: relative;
    margin-left: -125px;
    &-mob {
      display: none;
    }
    img {
      position: relative;
      z-index: 1;
      width: 100%;
    }
    &:after {
      content: '';
      position: absolute;
      top: 100px;
      left: 50%;
      transform: translate(-50%, 0);
      width: 420px;
      height: 420px;
      background: #BFADA3;
      filter: blur(150px);
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 0.8fr 1.2fr;
    gap: 40px 20px;
    margin-top: 25px;
  }
  &__item {
    &--two {
      grid-column: 1/3;
    }
    &-desc {
      p {
        padding-left: 100px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 10px;
          left: 0;
          width: 50px;
          height: 50px;
          background-image: url("../img/about-icon.svg");
          background-repeat: no-repeat;
          background-position: center;
          background-size: cover;
        }
        &:not(:first-child) {
          margin-top: 40px;
        }
      }
    }
  }
  &__text {
    font-size: 30px;
    line-height: 120%;
    text-align: justify;
    &2 {
      color: #BF755A;
    }
  }
  &__quote {
    display: flex;
    align-items: center;
    font-size: 30px;
    line-height: 120%;
    margin-top: 35px;
    position: relative;
    span {
      margin-left: 30px;
    }
    &:after {
      content: '';
      position: absolute;
      top: -10px;
      left: 0;
      width: 250px;
      height: 2px;
      background: #BFADA3;
    }
  }
}
.planet {
  padding: 77px 0;
  &__title {
    font-size: 64px;
    line-height: 105%;
    text-align: center;
    text-transform: uppercase;
    color: #BFADA3;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 100px 50px;
    margin-top: 100px;
  }
  &__item {
    position: relative;
    &:nth-child(even) {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: -27px;
        width: 2px;
        height: 100%;
        background: #BFADA3;
      }
    }
    &-img {
      width: 377px;
      margin: 0 auto;
      img {
        width: 100%;
      }
    }
  }
  &__desc {
    font-size: 30px;
    line-height: 105%;
    margin-top: 40px;
    span {
      text-transform: uppercase;
    }
  }
  &__img {
    max-width: 1085px;
    margin: -120px auto -77px;
    &-mob {
      display: none;
    }
    img {
      width: 100%;
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    position: relative;
    z-index: 2;
    &-back {
      display: flex;
      align-items: center;
      svg {
        transition: all 0.5s;
      }
      span {
        font-size: 28px;
        line-height: 120%;
        margin-left: 15px;
        position: relative;
        padding-bottom: 2px;
        border-bottom: 0.5px solid #BFADA3;
      }
      &:hover {
        span {
          border-bottom: 0.5px solid #A61420;
        }
        svg path {
          fill: #A61420;
        }
      }
    }
  }
  &__sun {
    color: #484848;
  }
  &__moon {
    color: #FFFFFF;
  }
  &__mercury {
    color: #78B96E;
  }
  &__venus {
    color: #FAB6CB;
  }
  &__mars {
    color: #FB0606;
  }
  &__jupiter {
    color: #9500C9;
  }
  &__saturn {
    color: #484848;
  }
  &__uranium {
    color: #00FF19;
  }
  &__neptune {
    color: #2AB9A8;
  }
  &__pluto {
    color: #761019;
  }
}
.services {
  padding: 60px 0;
  &__inner {
    max-width: 972px;
    margin: 0 auto;
  }
  &__title {
    font-size: 80px;
    line-height: 105%;
    color: #A61420;
    text-align: center;
  }
  &__subtitle {
    font-size: 48px;
    line-height: 105%;
    margin-top: 80px;
    text-align: center;
  }
  &__info {
    margin-top: 50px;
    border: 3px solid #BFADA3;
    font-size: 32px;
    line-height: 105%;
    color: #ffffff;
    padding: 23px;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr;
    gap: 215px;
    margin-top: 200px;
  }
  &__item {
    &-wrapp {
      display: flex;
      justify-content: space-between;
      padding: 15px;
      position: relative;
      min-height: 372px;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 21%;
        height: 4.7px;
        background: #BFADA3;
      }
      &:before {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        width: 21%;
        height: 4.7px;
        background: #BFADA3;
      }
    }
    .border-white {
      border: 4.7px solid #BFADA3;
      border-bottom: unset;
    }
    .border-red {
      border: 4.7px solid #A61420;
      border-bottom: unset;
      &:after {
        background: #A61420;
      }
      &:before {
        background: #A61420;
      }
    }
    &-left {
      max-width: 197px;
      flex-shrink: 0;
    }
    &-center {
      width: auto;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      &:after {
        content: '';
        position: absolute;
        z-index: -1;
        top: -10px;
        right: 40px;
        width: 280px;
        height: 280px;
      }
    }
    &-right {
      width: 292px;
      flex-shrink: 0;
    }
    .bg-red {
      &:after {
        background: #A61420;
        border: 3.07322px solid #000000;
        filter: blur(91.8387px);
      }
    }
    .bg-white {
      top: -80px;
      &:after {
        background: #FFFEFE;
        border: 3.05741px solid #000000;
        filter: blur(91.7222px);
      }
    }
    .opacity {
      img {
        opacity: 0;
        pointer-events: none;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 130%;
      letter-spacing: 0.07em;
      text-transform: uppercase;
      span {
        display: block;
      }
    }
    &-info {
      font-size: 24px;
      line-height: 110%;
      letter-spacing: 0.07em;
      text-transform: uppercase;
      color: #BF755A;
    }
    &-btn {
      width: 120px;
      height: 120px;
      display: block;
      margin: 60px auto 0;
      svg {
        width: 100%;
        height: auto;
      }
      &:hover {
        svg ellipse {
          fill: #f34343;
        }
      }
    }
    &-btn2 {
      position: relative;
      width: 280px;
      height: 80px;
      border-radius: 100px;
      font-size: 30px;
      line-height: 120%;
      text-transform: capitalize;
      color: #ffffff;
      margin: 25px auto 0;
      img {
        position: absolute;
        right: -50px;
        bottom: 0;
      }
    }
    &-btn3 {
      position: relative;
      width: 280px;
      height: 80px;
      background: #7B7775;
      border-radius: 100px;
      font-size: 30px;
      line-height: 120%;
      text-transform: capitalize;
      color: #ffffff;
      margin: 25px auto 0;
      &:hover {
        background: #575757;
        color: #ffffff;
      }
      img {
        position: absolute;
        right: -40px;
        bottom: 0;
      }
    }
    &-icon {
      z-index: -1;
      max-width: 84px;
      height: auto;
      top: -20px;
      left: -55px;
    }
    &-title {
      font-size: 36px;
      line-height: 105%;
      text-transform: uppercase;
      color: #F25E5E;
      margin-top: -20px;
    }
    &-title2 {
      color: #FFFEFE;
    }
    &-bottom {
      position: relative;
      z-index: 2;
      text-align: center;
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    position: relative;
    z-index: 2;
    &-wrapp {
      display: flex;
      align-items: center;
      svg {
        transition: all 0.5s;
      }
      span {
        font-size: 28px;
        line-height: 120%;
        margin-left: 15px;
      }
      &:hover {
        svg path {
          fill: #A61420;
        }
      }
    }
    &-back {
      display: flex;
      align-items: center;
      svg {
        transition: all 0.5s;
      }
      span {
        font-size: 28px;
        line-height: 120%;
        margin-left: 15px;
        position: relative;
        padding-bottom: 2px;
        border-bottom: 0.5px solid #BFADA3;
      }
      &:hover {
        svg path {
          fill: #A61420;
        }
      }
    }
  }
  &__img {
    max-width: 1085px;
    margin: -120px auto -77px;
    img {
      width: 100%;
    }
  }
}
