    &__item {
      &-wrapp {
        min-height: 200px;
        &:before {
          height: 1.5px;
        }
        &:after {
          height: 1.5px;
        }
      }
      .border-white {
        border: 1.5px solid #BFADA3;
        border-bottom: unset;
      }
      .border-red {
        border: 1.5px solid #A61420;
        border-bottom: unset;
      }
      &-left {
        max-width: 120px;
      }
      &-center {
        &:after {
          top: 0;
          right: 0;
          width: 90px;
          height: 90px;
          filter: blur(27px)!important;
        }
        img {
          max-width: 200px;
        }
      }
      &-right {
        width: 120px;
      }
      &-text {
        font-size: 10px;
        img {
          width: 20px;
        }
      }
      &-info {
        font-size: 10px;
      }
      &-btn {
        width: 40px;
        height: 40px;
        margin-top: 30px;
      }
      &-btn2 {
        width: 147px;
        font-size: 14px;
        margin-top: 10px;
        img {
          right: 0;
        }
      }
      &-btn3 {
        width: 147px;
        height: 40px;
        font-size: 14px;
        margin-top: 10px;
      }
      &-title {
        font-size: 18px;
        margin-top: -10px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .about {
    &__title {
      font-size: 58px;
    }
    &__item {
      &--three {
        width: calc(100% + 30px);
        margin-left: -15px;
      }
    }
    &__img {
     max-width: 100%;
    }
  }
  .planet {
    &__item {
      &-img {
        width: 150px;
        height: 100%;
        img {
          height: 100%;
        }
      }
    }
    &__desc {
      font-size: 14px;
    }
    &__link-back {
      span {
        font-size: 12px;
        margin-left: 7px;
      }
      svg {
        width: 13px;
        height: auto;
      }
    }
  }
  .services {
    &__subtitle {
      font-size: 14px;
    }
    &__info {
      font-size: 12px;
      margin-top: 15px;
    }
    &__item {
      &-wrapp {
        min-height: 121px;
        padding: 10px;
      }
      &-left {
        max-width: 65px;
      }
      &-center {
        top: 0 !important;
        bottom: unset !important;
        img {
          max-width: 150px;
        }
      }
      &-right {
        width: 85px;
      }
      &-text {
        font-size: 5px;
      }
      &-info {
        font-size: 7px;
      }
      &-title {
        font-size: 14px;
      }
    }
    &__link-wrapp {
      svg {
        width: 13px;
      }
      span {
        font-size: 12px;
      }
    }
    &__link-back {
      span {
        font-size: 12px;
        margin-left: 7px;
      }
      svg {
        width: 13px;
        height: auto;
      }
    }
  }
  .connection {
    &__form {
      &2 {
        border: 3px solid #720e16;
        padding: 30px 20px;
      }
      &-wrapp {
        padding: 30px 20px;
      }
      &-wrapp2 {
        border: unset;
        padding: 0;
      }
      &-title {
        font-size: 22px;
        margin-bottom: 35px;
      }
      &-group {
        &:not(:last-child) {
          margin-bottom: 30px;
        }
      }
      &-btn {
        margin-top: 57px;
      }
    }
    &__link {
      margin-top: 26px;
      &-back {
        svg {
          width: 13px;
          height: auto;
        }
        span {
          font-size: 12px;
          margin-left: 7px;
        }
      }
    }
  }
}

@media screen and (max-width: 360px) {
  .about {
    &__title {
      font-size: 48px;
    }
  }
}