.horoscope {
  background: #5AC0C3;
  overflow: hidden;
  .container {
    max-width: 1370px;
  }
  &__top {
    min-height: 270px;
    background-image: url("../img/horoscope-bg.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 270px;
    padding-top: 30px;
    position: relative;
  }
  &__img {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
  }
  &__star {
    position: absolute;
    z-index: 0;
    left: 10px;
    bottom: 30px;
  }
  &__star2 {
    position: absolute;
    z-index: 0;
    top: 25px;
    left: 17%;
  }
  &__star3 {
    position: absolute;
    z-index: 0;
    top: 90px;
    left: 40%;
  }
  &__star4 {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
  }
  &__star5 {
    position: absolute;
    z-index: 4;
    top: 0;
    left: 14%;
  }
  &__star6 {
    position: absolute;
    z-index: 4;
    top: 0;
    left: 34%;
  }
  &__star7 {
    position: absolute;
    z-index: 4;
    top: -80px;
    right: 30%;
  }
  &__star8 {
    display: none;
  }
  &__star9 {
    position: absolute;
    z-index: 4;
    left: 4%;
    bottom: 90px;
  }
  &__star10 {
    position: absolute;
    z-index: 4;
    top: 0;
    left: 16%;
  }
  &__star11 {
    position: absolute;
    top: 25px;
    right: 11%;
  }
  &__top-wrapp {
    position: relative;
    z-index: 1;
  }
  &__title {
    font-size: 56px;
    line-height: 100%;
    background: linear-gradient(180deg, #F7ED7E 0%, #E5AF4F 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
  }
  &__info {
    margin-top: 30px;
    position: relative;
    padding: 160px 0 85px;
  }
  &-bg2 {
    position: absolute;
    z-index: 2;
    top: -20px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 100%;
    height: 371px;
    pointer-events: none;
  }
  &-bg3 {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 0);
    width: 100%;
    height: 94px;
    pointer-events: none;
  }
  &-bg__mob2 {
    display: none;
  }
  &-bg__mob3 {
    display: none;
  }
  &__info-wrapp {
    background: #FFFFFF;
    position: relative;
    z-index: 3;
    padding: 0 15px;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 95px;
  }
  &__item {
   &2 {
     margin-top: 60px;
   }
    &3 {
      margin-top: -60px;
    }
   &-top {
     position: relative;
     &:after {
       content: '';
       position: absolute;
       top: 0;
       left: 0;
       width: 4px;
       height: calc(100% - 40px);
       background: #F1D95D;
     }
   }
    &-bottom {
      position: relative;
      margin: 60px 0 0 50px;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: calc(100% - 40px);
        background: #F1D95D;
      }
    }
    &-wrapp {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 4px;
        height: calc(100% - 40px);
        background: #F1D95D;
      }
    }
    &-title {
      font-weight: 700;
      font-size: 30px;
      line-height: 105%;
      color: #7B7877;
      margin-bottom: 50px;
      padding-left: 50px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: -29px;
        width: 60px;
        height: 60px;
        box-shadow: 0px 7.0957px 7.0957px rgba(0, 0, 0, 0.25);
        border-radius: 50%;
      }
    }
    &-desc {
      p {
        font-size: 26px;
        line-height: 105%;
        color: #7B7877;
        padding-left: 50px;
        position: relative;
        &:not(:first-child) {
          margin-top: 40px;
        }
        &:after {
          content: '';
          position: absolute;
          z-index: 1;
          top: 50%;
          left: -16px;
          transform: translate(0, -50%);
          width: 34px;
          height: 34px;
          background: #F1D95D;
          border-radius: 50%;
        }
      }
    }
    .red {
      &:after {
        background: #FB0606;
      }
    }
    .green {
      &:after {
        background: #00FF19;
      }
    }
    .blue {
      &:after {
        background: #2F0EFA;
      }
    }
    .pink {
      &:after {
        background: #D55272;;
      }
    }
    .orange {
      &:after {
        background: #FF7B02;
      }
    }
  }
  &__bottom {
    position: relative;
    padding: 40px 0 160px;
  }
  &-bg4 {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 106px;
    pointer-events: none;
  }
  &-bg__mob4 {
    display: none;
  }
  &__price {
    font-size: 36px;
    line-height: 105%;
    text-transform: uppercase;
    color: #F25E5E;
    text-align: center;
    span {
      margin-left: 32px;
    }
  }
  &__info2 {
    font-size: 32px;
    line-height: 105%;
    color: #FFFFFF;
    margin-top: 30px;
    text-align: center;
  }
}

@media screen and (max-width: 1400px) {
  .horoscope {
    &__items {
      gap: 50px;
    }
   &__item {
     &-title {
       font-size: 26px;
       margin-bottom: 40px;
     }
     &-desc {
       p {
         font-size: 20px;
         &:not(:first-child) {
           margin-top: 30px;
         }
       }
     }
   }
  }
}

@media screen and (max-width: 992px) {
  .horoscope {
    &__star {
      width: 60px;
    }
    &__star2 {
      width: 50px;
    }
    &__star3 {
      width: 50px;
    }
    &__star4 {
      width: 150px;
    }
    &__star5 {
      left: unset;
      right: 27%;
      width: 40px;
    }
    &__star6 {
      top: 50%;
      left: unset;
      right: 0;
      width: 30px;
      transform: translate(0, -50%);
    }
    &__star7 {
      top: unset;
      right: 0;
      bottom: 20px;
      width: 65px;
    }
    &__items {
      grid-template-columns: 1fr;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        width: 2px;
        height: calc(100% - 20px);
        background: #F1D95D;
      }
    }
    &__item {
      &2 {
        margin: 0;
      }
      &3 {
        margin: 0;
      }
      &-wrapp {
        &:after {
          display: none;
        }
      }
      &-top {
        &:after {
          display: none;
        }
      }
      &-bottom {
        margin: 40px 0 0 0;
        &:after {
          display: none;
        }
      }
      &-title {
        padding-left: 30px;
        &:after {
          top: -5px;
          left: -19px;
          width: 40px;
          height: 40px;
        }
      }
      &-desc {
        p {
          padding-left: 30px;
          &:after {
            left: -10px;
            width: 20px;
            height: 20px;
          }
        }
      }
    }
    &__star9 {
      top: -50px;
      bottom: unset;
    }
    &__star10 {
      display: none;
    }
    &__star11 {
      top: unset;
      right: 5px;
      bottom: 50px;
      width: 40px;
    }
    &-bg4 {
      height: 80px;
    }
    &__price {
      font-size: 28px;
    }
    &__info2 {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 768px) {
  .horoscope {
    &__top {
      min-height: 200px;
      background-image: url("../img/horoscope__mob-bg.png");
      background-position: top;
      background-size: 100% 200px;
      padding-top: 20px;
    }
    &__star2 {
      top: unset;
      left: 20%;
      bottom: 30px;
    }
    &__star3 {
      left: 90%;
    }
    &__star4 {
      top: unset;
      left: 10px;
      bottom: -80px;
    }
    &__title {
      font-size: 42px;
    }
    &-bg2 {
      display: none;
    }
    &-bg3 {
      display: none;
    }
    &-bg__mob2 {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 200px;
    }
    &-bg__mob3 {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 100px;
    }
    &__price {
      font-size: 24px;
    }
    &__info2 {
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .horoscope {
    &__top {
      min-height: 160px;
      background-size: 100% 160px;
    }
    &__star {
      top: unset;
      bottom: 0;
      width: 40px;
    }
    &__star2 {
      top: unset;
      bottom: 0;
      width: 30px;
    }
    &__star3 {
      left: 85%;
    }
    &__star4 {
      z-index: 1;
      width: 80px;
    }
    &__title {
      font-size: 32px;
    }
    &__info {
      padding: 100px 0 60px;
    }
    &__item {
      &-title {
        font-size: 22px;
      }
    }
    &-bg__mob2 {
      height: 160px;
    }
    &__price {
      font-size: 20px;
    }
    &__info2 {
      font-size: 18px;
    }
  }
}

@media screen and (max-width: 450px) {
  .horoscope {
    &__top {
      min-height: 130px;
      background-size: 100% 130px;
    }
    &__title {
      font-size: 30px;
    }
    &__items {
      gap: 20px;
      &:after {
        height: calc(100% - 10px);
      }
    }
    &__item {
      &-bottom {
        margin-top: 20px;
      }
      &-title {
        font-size: 14px;
        margin-bottom: 20px;
        padding-left: 20px;
        &:after {
          left: -7px;
          width: 16px;
          height: 16px;
        }
      }
      &-desc {
        p {
          padding-left: 20px;
          font-size: 12px;
          &:not(:first-child) {
            margin-top: 10px;
          }
          &:after {
            left: -4px;
            width: 10px;
            height: 10px;
          }
        }
      }
    }
    &__bottom {
      padding: 50px 25px 80px;
    }
    &-bg4 {
      display: none;
    }
    &-bg__mob4 {
      display: block;
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 56px;
    }
    &__price {
      font-size: 16px;
      display: flex;
      justify-content: space-between;
    }
    &__info2 {
      font-size: 14px;
      margin-top: 15px;
    }
  }
}

@media screen and (max-width: 380px) {
  .horoscope {
    &__star8 {
      display: block;
      position: absolute;
      z-index: 4;
      top: 200px;
      right: 0;
      width: 90px;
    }
    &__info {
      padding: 60px 0;
    }
    &-bg__mob2 {
      height: 90px;
    }
    &-bg__mob3 {
      height: 60px;
    }
  }
}

@media screen and (max-width: 360px) {
  .horoscope {
    &__star7 {
      display: none;
    }
    &__star8 {
      display: none;
    }
  }
}
