.finance {
  background: #FFFFFF;
  padding: 40px 0 77px;
  position: relative;
  &:after {
    content: '';
    position: absolute;
    z-index: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 33px;
    background: linear-gradient(180deg, #F7ED7E 0%, #E5AF4F 100%);
  }
  &__img2 {
    position: absolute;
    right: 20px;
    bottom: 70px;
    max-width: 258px;
  }
  &__img3 {
    display: none;
  }
  &__inner {
    position: relative;
  }
  &__img {
    position: absolute;
    z-index: 0;
    top: 0;
    right: 0;
    max-width: 393px;
  }
  &__wrapp {
    position: relative;
    z-index: 2;
  }
  &__title {
    font-size: 56px;
    line-height: 100%;
    background: linear-gradient(180deg, #F7ED7E 0%, #E5AF4F 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
    text-align: center;
  }
  &__list {
    margin-top: 45px;
  }
  &__item {
    display: flex;
    align-items: center;
    &:not(:first-child) {
      margin-top: 20px;
    }
    &-icon {
      width: 50px;
    }
    &-desc {
      font-weight: 700;
      font-size: 24px;
      line-height: 100%;
      color: #664E1E;
      margin-left: 34px;
      span {
        text-transform: uppercase;
      }
    }
  }
  &__price {
    font-size: 32px;
    line-height: 105%;
    color: #F25E5E;
    text-align: center;
    margin-top: 45px;
    span {
      margin-left: 32px;
    }
  }
  &__info {
    font-size: 28px;
    line-height: 105%;
    margin-top: 25px;
    text-align: center;
    span {
      margin-top: 10px;
      br {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .finance {
    &__img {
      max-width: 300px;
    }
    &__img2 {
      max-width: 150px;
    }
  }
}

@media screen and (max-width: 992px) {
  .finance {
    &__img {
      max-width: 250px;
    }
    &__title {
      font-size: 42px;
    }
    &__item {
      &-icon {
        width: 40px;
      }
      &-desc {
        font-size: 20px;
      }
    }
    &__price {
      text-align: start;
      font-size: 28px;
    }
    &__info {
      text-align: start;
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 768px) {
  .finance {
    padding: 0 0 77px;
    &__inner {
      padding-top: 250px;
    }
    &__img {
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 100%;
    }
    &__img2 {
      max-width: 100px;
    }
    &__title {
      font-size: 36px;
    }
    &__item {
      align-items: end;
      &-icon {
        width: 30px;
      }
      &-desc {
        font-size: 18px;
        margin-left: 20px;
      }
    }
    &__price {
      font-size: 24px;
    }
    &__info {
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 576px) {
  .finance {
    &__img2 {
      display: none;
    }
    &__img3 {
      position: absolute;
      right: 0;
      bottom: 50px;
      display: block;
      max-width: 140px;
    }
    &__title {
      font-size: 28px;
    }
    &__item {
      &-desc {
        font-size: 16px;
      }
    }
    &__price {
      font-size: 20px;
    }
    &__info {
      font-size: 18px;
      span br {
        display: block;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .finance {
    padding: 0 0 60px 0;
    &__inner {
      padding-top: 275px;
    }
    &__img {
      max-width: 240px;
    }
    &__list {
      margin-top: 25px;
    }
    &__item {
      &-img {
        width: 20px;
      }
      &-desc {
        font-size: 12px;
        margin-left: 25px;
      }
    }
    &__price {
      font-size: 16px;
      span {
        margin-left: 45px;
      }
    }
    &__info {
      font-size: 14px;
      margin-top: 45px;
    }
  }
}

@media screen and (max-width: 360px) {
  .finance {
    &__img3 {
      max-width: 100px;
    }
  }
}
