.order {
  padding: 60px 0;
  position: relative;
  overflow: hidden;
  &__inner {
    max-width: 1075px;
    margin: 0 auto;
  }
  &__form {
    &-wrapp {
      border: 4.7px solid #A61420;
      padding: 45px 70px;
      max-width: 540px;
      width: 100%;
    }
    &-title {
      font-size: 40px;
      line-height: 120%;
      text-align: center;
      margin-bottom: 30px;
    }
    &-group {
      &:not(:last-child) {
        margin-bottom: 35px;
      }
      input {
        width: 100%;
        height: 35px;
      }
    }
    &-desc {
      font-size: 30px;
      line-height: 105%;
      margin-top: 70px;
    }
    &-price {
      font-size: 36px;
      line-height: 120%;
      text-transform: uppercase;
      margin-top: 80px;
      text-align: center;
      span {
        margin-left: 50px;
      }
    }
    &-btn {
      width: 280px;
      height: 80px;
      margin: 50px auto 0;
    }
    &-check {
      display: inline-flex;
      align-items: center;
      position: relative;
      margin-top: 30px;
      input {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        cursor: pointer;
        opacity: 0;
        &:checked~label:before {
          opacity: 1;
        }
      }
      label {
        font-size: 24px;
        line-height: 105%;
        padding-left: 70px;
        position: relative;
        a {
          position: relative;
          z-index: 3;
        }
        &:after {
          content: '';
          position: absolute;
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 30px;
          height: 30px;
          border: 3px solid #A61420;
        }
        &:before {
          content: '';
          position: absolute;
          top: 50%;
          left: 5.5px;
          transform: translate(0, -50%);
          width: 20px;
          height: 15px;
          background-image: url("../img/check.svg");
          background-position: center;
          background-repeat: no-repeat;
          background-size: 20px 15px;
          opacity: 0;
          transition: all 0.3s;
        }
      }
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    margin-top: 100px;
    position: relative;
    z-index: 2;
    &-back {
      display: flex;
      align-items: center;
      svg {
        transition: all 0.5s;
      }
      span {
        font-size: 28px;
        line-height: 120%;
        margin-left: 15px;
        position: relative;
        padding-bottom: 2px;
        border-bottom: 0.5px solid #BFADA3;
      }
      &:hover {
        span {
          border-bottom: 0.5px solid #A61420;
        }
        svg path {
          fill: #A61420;
        }
      }
    }
  }
  &__img {
    display: none;
  }
}

@media screen and (max-width: 992px) {
  .order {
    &__form {
      &-title {
        font-size: 32px;
      }
      &-desc {
        font-size: 24px;
      }
      &-price {
        font-size: 28px;
        span {
          margin-left: 32px;
        }
      }
      &-check {
        label {
          font-size: 20px;
          padding-left: 50px;
        }
      }
    }
    &__link-back {
      span {
        font-size: 24px;
      }
      svg {
        width: 30px;
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .order {
    &__form {
      &-wrapp {
        padding: 25px;
      }
      &-title {
        font-size: 28px;
        margin-bottom: 35px;
      }
      &-desc {
        font-size: 20px;
        margin-top: 45px;
      }
      &-price {
        font-size: 24px;
        margin-top: 45px;
      }
      &-btn {
        width: 220px;
        height: 47px;
      }
      &-check {
        label {
          font-size: 18px;
        }
      }
    }
    &__link {
      margin-top: 30px;
    }
    &__link-back {
      span {
        font-size: 20px;
      }
      svg {
        width: 25px;
        height: auto;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .order {
    padding: 0;
    &__inner {
      padding-top: 400px;
    }
    &__form {
      &-wrapp {
        border: 3px solid #A61420;
      }
      &-desc {
        font-size: 18px;
      }
      &-price {
        font-size: 20px;
      }
      &-check {
        label {
          font-size: 16px;
          padding-left: 30px;
          &:after {
            top: 2px;
            transform: translate(0, 0);
            width: 20px;
            height: 20px;
            border: 2px solid #A61420;
          }
          &:before {
            top: 8px;
            transform: translate(0, 0);
            width: 10px;
            height: 7px;
            background-size: 14px 7px;
          }
        }
      }
    }
    &__link-back {
      span {
        font-size: 16px;
      }
      svg {
        width: 20px;
      }
    }
    &__img {
      display: block;
      width: 100%;
      margin-top: -50px;
      img {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .order {
    &__inner {
      padding-top: 230px;
    }
    &__form {
      &-title {
        font-size: 24px;
      }
      &-price {
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 16px;
        span {
          margin-left: 15px;
        }
      }
      &-check {
        label {
          font-size: 14px;
          padding-left: 27px;
          &:after {
            width: 14px;
            height: 14px;
          }
          &:before {
            top: 6px;
            left: 2.5px;
            width: 9px;
            height: 6px;
            background-size: 9px 6px;
          }
        }
      }
    }
    &__link-back {
      span {
        font-size: 12px;
        margin-left: 7px;
      }
      svg {
        width: 13px;
        height: auto;
      }
    }
  }
}