.western-astrology {
  padding: 70px 0;
  position: relative;
  overflow: hidden;
  &__title {
    font-size: 34px;
    line-height: 150%;
  }
  &__list {
    margin-top: 50px;
    li {
      display: flex;
      align-items: center;
      font-size: 32px;
      line-height: 105%;
      &:not(:first-child) {
        margin-top: 15px;
      }
      span {
        margin-left: 30px;
      }
      img {
        width: 30px;
        img {
          width: 100%;
        }
      }
    }
  }
  &__desc {
    margin-top: 50px;
    p {
      font-size: 32px;
      line-height: 105%;
      span {
        color: #F25E5E;
      }
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
  &__text {
    color: #BF755A;
  }
  &__btns {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
  }
  &__btn {
    width: 337px;
    height: 80px;
    background: transparent;
    border: 3px solid #A61420;
    border-radius: 50px;
    font-size: 30px;
    line-height: 120%;
    text-transform: uppercase;
    &:hover {
      background: #A61420;
      color: #BFADA3;
    }
  }
  &__btn2 {
    width: 337px;
    height: 80px;
    background: #A61420;
    border: 3px solid transparent;
    border-radius: 50px;
    font-size: 30px;
    line-height: 120%;
    text-transform: uppercase;
    margin-left: 65px;
    &:hover {
      background: transparent;
      border: 3px solid #A61420;
      color: #BFADA3;
    }
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    &-wrapp {
      display: flex;
      align-items: center;
      &:hover {
        svg path {
          fill: #A61420;
        }
        span {
          border-bottom: 0.5px solid #A61420;
        }
      }
      span {
        font-size: 28px;
        line-height: 120%;
        margin-left: 10px;
        padding-bottom: 2px;
        border-bottom: 0.5px solid #BFADA3;
      }
      svg {
        transition: all 0.5s;
      }
    }
  }
}
.ball {
  position: absolute;
  z-index: -1;
  top: -300px;
  right: 0;
  width: 700px;
  transform: rotate(-45deg);
  figure {
    margin: 0;
  }
  img {
    width: 100%;
  }
}

@media screen and (max-width: 992px) {
  .western-astrology {
    &__title {
      font-size: 28px;
    }
    &__list {
      margin-top: 25px;
      li {
        font-size: 24px;
      }
      li img {
        width: 25px;
      }
    }
    &__desc {
      margin-top: 25px;
      p {
        font-size: 24px;
      }
    }
    &__btns {
      margin-top: 45px;
    }
    &__btn {
      width: 250px;
      height: 60px;
      font-size: 24px;
    }
    &__btn2 {
      width: 250px;
      height: 60px;
      font-size: 24px;
      margin-left: 30px;
    }
    &__link {
      margin-top: 37px;
      &-wrapp {
        span {
          font-size: 24px;
        }
        svg {
          width: 30px;
          height: auto;
        }
      }
    }
  }
  .ball {
    top: -200px;
    width: 500px;
  }
}

@media screen and (max-width: 576px) {
  .western-astrology {
    padding: 400px 0 50px;
    &__title {
      font-size: 24px;
      br {
        display: none;
      }
    }
    &__list {
      li {
       font-size: 20px;
      }
      li svg {
        width: 20px;
      }
      li span {
        margin-left: 12px;
      }
    }
    &__desc {
      p {
        font-size: 20px;
      }
    }
    &__btn {
      width: 180px;
      height: 50px;
      font-size: 18px;
    }
    &__btn2 {
      width: 180px;
      height: 50px;
      font-size: 18px;
      margin-left: 20px;
    }
    &__link {
      &-wrapp {
        svg {
          width: 25px;
        }
        span {
          font-size: 20px;
        }
      }
    }
  }
  .ball {
    top: 0;
    width: 100%;
    height: 400px;
    transform: rotate(0);
    figure {
      height: 100%;
    }
    img {
      height: 100%;
    }
  }
}

@media screen and (max-width: 450px) {
  .western-astrology {
    padding-top: 230px;
    &__title {
      font-size: 17px;
    }
    &__list {
      li {
        font-size: 16px;
        &:not(:first-child) {
          margin-top: 10px;
        }
      }
      li img {
        width: 15px;
      }
    }
    &__desc {
      p {
        font-size: 16px;
      }
    }
    &__btn {
      width: 150px;
      height: 40px;
      font-size: 14px;
    }
    &__btn2 {
      width: 150px;
      height: 40px;
      font-size: 14px;
    }
    &__link {
      &-wrapp {
        svg {
          width: 13px;
        }
        span {
          font-size: 12px;
          margin-left: 7px;
        }
      }
    }
  }
  .ball {
    height: 230px;
  }
}
