.header {
  padding: 60px 0 20px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    font-size: 36px;
    line-height: 120%;
    color: #BFADA3;
  }
  &__list {
    display: flex;
    align-items: center;
    li {
      font-size: 16px;
      line-height: 120%;
      &:not(:first-child) {
        margin-left: 36px;
      }
    }
  }
  &__tel {
    font-size: 20px;
    line-height: 120%;
    margin-left: 36px;
  }
  &__menu {
    margin-left: 130px;
    position: relative;
  }
  &__menu-inner {
    position: absolute;
    z-index: 3;
    top: -1000px;
    right: 0;
    width: 312px;
    background: #000000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    overflow: hidden;
    padding: 125px 25px 25px;
    transition: all 0.5s;
    &--active {
      top: -70px;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 230px;
      height: 230px;
      background: #BFADA3;
      opacity: 0.56;
      filter: blur(150px);
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      right: 0;
      bottom: 0;
      width: 245px;
      height: 245px;
      background: #BFADA3;
      opacity: 0.8;
      filter: blur(150px);
      pointer-events: none;
    }
    li {
      font-size: 20px;
      line-height: 120%;
      &:not(:first-child) {
        margin-top: 32px;
      }
    }
  }
  &__burger {
    position: relative;
    z-index: 6;
    width: 79px;
    height: 30px;
    cursor: pointer;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2.5px;
      background: #BFADA3;
      transition: all 0.5s;
    }
    span:nth-child(1) {
      top: 0;
    }
    span:nth-child(2) {
      top: 50%;
      transform: translate(0, -50%);
    }
    span:nth-child(3) {
      bottom: 0;
    }
    &--active {
      margin-right: 20px;
      span:nth-child(1) {
        transform: rotate(-45deg);
        top: 13px;
      }
      span:nth-child(2) {
        display: none;
      }
      span:nth-child(3) {
        transform: rotate(45deg);
        bottom: 13px;
      }
    }
  }
  &__item {
    &-show {
      display: flex;
      align-items: center;
      cursor: pointer;
      span {
        font-size: 20px;
        line-height: 120%;
        margin-right: 12px;
        margin-top: -8px;
      }
      svg path {
        stroke:  #BFADA3;;
      }
      &:hover {
        span {
          color: #A61420;
        }
        svg path {
          stroke: #A61420;
        }
      }
      &--active {
        span {
          color: #A61420;
        }
        svg path {
          stroke: #A61420;
        }
      }
    }
    &-hidden {
      display: none;
      margin-top: 15px;
    }
    &-link {
      font-size: 16px;
      line-height: 120%;
      display: block;
      &:not(:first-child) {
        margin-top: 12px;
      }
    }
  }
  &__btn {
    width: 100%;
    height: 50px;
    border: 1px solid #BF755A;
    border-radius: 42px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    line-height: 120%;
    color: #BF755A;
    margin-top: 40px;
    &:hover {
      background: #BF755A;
      border: 1px solid transparent;
      color: #FFFFFF;
    }
  }
}

@media screen and (max-width: 1100px) {
  .header {
    &__logo {
      font-size: 28px;
    }
    &__list {
      &:not(:first-child) {
        margin-left: 25px;
      }
    }
    &__tel {
      margin-left: 25px;
    }
    &__menu {
      margin-left: 50px;
    }
    &__burger {
      width: 40px;
      height: 16px;
      span {
        height: 2px;
      }
      &--active {
        span:nth-child(1) {
          top: 7px;
        }
        span:nth-child(3) {
          bottom: 7px;
        }
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    padding-top: 30px;
    &__logo {
      font-size: 24px;
    }
    &__list {
      li {
        &:not(:first-child) {
          margin-left: 20px;
        }
      }
    }
    &__tel {
      font-size: 16px;
      margin-left: 20px;
    }
    &__menu {
      margin-left: 40px;
    }
    &__menu-inner {
      li {
        font-size: 16px;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__item {
      &-show {
        span {
          font-size: 16px;
        }
        svg {
          width: 15px;
          height: auto;
        }
      }
      &-link {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__logo {
      font-size: 20px;
    }
    &__list {
      display: none;
    }
    &__tel {
      display: none;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      font-size: 14px;
    }
    &__menu-inner {
      width: 250px;
    }
    &__btn {
      height: 40px;
      margin-top: 30px;
      font-size: 18px;
    }
  }
}